import { AfterViewInit, Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { ContextMenuComponent } from '../../../shared/context-menu/context-menu.component';
import { ApiService } from '../../../shared/services/api.service';

@Component({
    selector: 'app-add-filter-dialog',
    templateUrl: './add-filter-dialog.component.html',
    styleUrls: ['./add-filter-dialog.component.scss']
})
export class AddFilterDialogComponent implements OnInit, AfterViewInit {

    processing = false;
    selectedOperator = '';
    selectedValue = '';
    field: any = {values: []};
    defaultOperators = [];
    dialogReady = false;
    dialogOpenSubscription;

    @ViewChild(ContextMenuComponent) ContextMenuComponent;

    constructor(public dialogRef: MatDialogRef<AddFilterDialogComponent>,
                @Inject(MAT_DIALOG_DATA) public data: any,
                private apiService: ApiService) {
        if (this.dialogOpenSubscription) {
            this.dialogOpenSubscription.unsubscribe();
        }
    }
    ngAfterViewInit() {
        this.showField(this.data.fieldObj);
        setTimeout(()=>{
            this.dialogReady = true;
        }, 100);

    }
    ngOnInit() {
    }

    getOperators(){
        return this.field.operators?this.field.operators:this.defaultOperators;
    }

    getDefaultOperators() {

        this.defaultOperators = [
            {value: '=', title: 'is equal'},
            {value: '!=', title: 'is not equal'}
        ];

        if (this.field.valueType == 'date') {

            this.defaultOperators = this.defaultOperators.concat([
                {value: '>', title: 'is greater than'},
                {value: '<', title: 'is less than'},
                {value: 'isNull', title: 'is null'},
                {value: 'isNotNull', title: 'is not null'},
            ]);
        }

    }

    showField(fieldObj) {
        this.field = fieldObj;

        // if no value provided by default then load it from database
        if (this.field.values==undefined || (this.field.values!=undefined && !this.field.values.length) && this.field.valueType!='date') {
            // load field values
            this.loadFields();
        }

        this.getDefaultOperators();

    }

    add() {

        const data = {field: this.field.id, operator: this.selectedOperator, value: this.selectedValue};

        if (this.field.valueType == 'date' && ['isNull', 'isNotNull'].indexOf(this.selectedOperator) < 0) {

            let date = '';
            if (this.selectedValue != null) {

                const d: any = this.selectedValue;
                const m = parseInt(d.getMonth()) + 1;
                date = d.getFullYear() + '-' + (('0' + m).slice(-2)) + '-' + ('0' + d.getDate()).slice(-2);

            } else {

                date = '';
            }

            data.value = date;

        }

        this.dialogRef.close({status: 1, data: {filter: data}});

    }

    loadFields() {
        this.processing = true;
        this.apiService.call('/api/integrations/needles/getFieldValues', {field: this.field.id, dataType: this.data.dataType}).subscribe((response) => {
            if (response.success) {
                this.field.values = response.data.values;
            }
            this.processing = false;
        }, () => {
            this.processing = false;
        });
    }


}



