import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { NeedlesRoutingModule } from './needles-routing.module';
import { NeedlesPersonsComponent } from './needles-persons/needles-persons.component';
import { NeedlesComponent } from './needles.component';
import { SharedModule } from '../shared/shared.module';
import { AddFilterDialogComponent } from './needles-persons/add-filter-dialog/add-filter-dialog.component';
import { NeedlesFiltersComponent } from './needles-persons/needles-filters/needles-filters.component';
import { CreateFilterNameDialogComponent } from './needles-persons/create-filter-name-dialog/create-filter-name-dialog.component';
import { SavedFiltersDialogComponent } from './needles-persons/saved-filters-dialog/saved-filters-dialog.component';

@NgModule({
  imports: [
    CommonModule,
    NeedlesRoutingModule,
      SharedModule
  ],
  declarations: [
      NeedlesComponent,
      NeedlesPersonsComponent,
      AddFilterDialogComponent,
      NeedlesFiltersComponent,
      CreateFilterNameDialogComponent,
      SavedFiltersDialogComponent,

  ],
    entryComponents:[
        AddFilterDialogComponent,
        CreateFilterNameDialogComponent,
        SavedFiltersDialogComponent
    ]
})
export class NeedlesModule { }



