import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { AddFilterDialogComponent } from '../add-filter-dialog/add-filter-dialog.component';

@Component({
    selector: 'app-create-filter-name-dialog',
    templateUrl: './create-filter-name-dialog.component.html',
    styleUrls: ['./create-filter-name-dialog.component.scss']
})
export class CreateFilterNameDialogComponent implements OnInit {

    name='';

    constructor(
        public dialogRef: MatDialogRef<CreateFilterNameDialogComponent>
    ) {
    }

    ngOnInit() {
    }

    saveClicked() {
      this.dialogRef.close({status:1, data:{name: this.name}});
    }

}



