import { Component, Inject, OnInit } from '@angular/core';
import { ApiService } from '../../../shared/services/api.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';

@Component({
    selector: 'app-saved-filters-dialog',
    templateUrl: './saved-filters-dialog.component.html',
    styleUrls: ['./saved-filters-dialog.component.scss']
})
export class SavedFiltersDialogComponent implements OnInit {

    filters = [];
    filtersLoading = false;

    constructor(private apiService: ApiService,
                @Inject(MAT_DIALOG_DATA) public data: any,
                private dialogRef: MatDialogRef<SavedFiltersDialogComponent>) {
    }


    ngOnInit() {
        this.loadFilters();
    }

    loadFilters() {
        this.filtersLoading = true;
        this.apiService.call('/api/needles/getFilters', {dataType: this.data.dataType}).subscribe((response: any) => {
            if (response.success) {
                this.filters = this.parseFilters(response.data.filters);
                console.log(this.filters);

            }
            this.filtersLoading = false;
        }, () => {
            this.filtersLoading = false;
        });

    }

    jsonParse(obj) {
        try {
            return JSON.parse(obj);
        } catch (e) {
            return [];
        }

    }

    parseFilters(f){
        f.forEach((obj: any, i)=>{
            obj.filters = JSON.parse(obj.filters);
            f[i] = obj;
        });

        return f;
    }

    load(filters){
        this.dialogRef.close({status:1, data:{filters}});
    }

}



