import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NeedlesComponent } from './needles.component';
import { NeedlesPersonsComponent } from './needles-persons/needles-persons.component';

const routes: Routes = [
    {path: '',   component: NeedlesComponent , children: [
            {path: ':dataType', component: NeedlesPersonsComponent},
        ]}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NeedlesRoutingModule { }



