import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material';
import { AddFilterDialogComponent } from './add-filter-dialog/add-filter-dialog.component';
import { ApiService } from '../../shared/services/api.service';
import { CreateTagsDialogComponent } from '../../shared/create-tags-dialog/create-tags-dialog.component';
import { Contact } from '../../contacts/contact';
import { GeneralService } from '../../shared/services/general.service';
import { NeedlesFiltersComponent } from './needles-filters/needles-filters.component';
import { CreateFilterNameDialogComponent } from './create-filter-name-dialog/create-filter-name-dialog.component';
import { SavedFiltersDialogComponent } from './saved-filters-dialog/saved-filters-dialog.component';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-needles-persons',
    templateUrl: './needles-persons.component.html',
    styleUrls: ['./needles-persons.component.scss']
})
export class NeedlesPersonsComponent implements OnInit {

    savingFilters = false;
    disableSaveFiltersBtn = true;
    addFilterDialogRef;
    createTagsDialogRef;
    showPersonsProgressBar = false;
    filters: any = [];
    persons = [];
    dataType = 'parties';

    // subscriptions
    addFilterDialogSubscription;
    createTagsDialogSubscription;
    getPersonsSubscription;
    createTagsSubscription;
    createFilterNameDialogAfterclosedSubscription;
    saveFiltersSubscription;
    saveFiltersDialogAfterClosedSubscription;

    @ViewChild(NeedlesFiltersComponent) NeedlesFiltersComponent: NeedlesFiltersComponent;

    constructor(public dialog: MatDialog,
                public apiService: ApiService,
                public generalService: GeneralService,
                private route: ActivatedRoute) {
    }

    ngOnInit() {
        this.route.params.subscribe(() => {
            this.reset();
            this.dataType = this.route.snapshot.params['dataType'];
            setTimeout(() => {
                this.NeedlesFiltersComponent.getContextMenuItems();
            }, 1);

        });

    }

    reset() {
        this.NeedlesFiltersComponent.removeAllFilters();
        this.filters = [];
        this.persons = [];
        this.unsubscribeAll();
    }

    unsubscribeAll() {
        if (this.addFilterDialogSubscription) {
            this.addFilterDialogSubscription.unsubscribe();
        }
        if (this.getPersonsSubscription) {
            this.getPersonsSubscription.unsubscribe();
        }
        if (this.createTagsDialogSubscription) {
            this.createTagsDialogSubscription.unsubscribe();
        }
        if (this.createTagsSubscription) {
            this.createTagsSubscription.unsubscribe();
        }

        if (this.createFilterNameDialogAfterclosedSubscription) {
            this.createFilterNameDialogAfterclosedSubscription.unsubscribe();
        }

        if (this.saveFiltersSubscription) {
            this.saveFiltersSubscription.unsubscribe();
        }

        if (this.saveFiltersDialogAfterClosedSubscription) {
            this.saveFiltersDialogAfterClosedSubscription.unsubscribe();
        }
    }

    openAddFiltersDialog(fieldObj) {
        this.addFilterDialogRef = this.dialog.open(AddFilterDialogComponent, {width: '700px', data: {fieldObj, dataType: this.dataType}});

        this.registerFilterDialogEvents();

    }

    registerFilterDialogEvents() {

        if (this.addFilterDialogSubscription) {
            this.addFilterDialogSubscription.unsubscribe();
        }
        this.addFilterDialogSubscription = this.addFilterDialogRef.afterClosed().subscribe((response) => {

            this.onAfterClosedAddFilterDialog(response);

        });

    }

    onAfterClosedAddFilterDialog(response) {
        if (response.status) {
            this.filters.push(response.data.filter);
            this.NeedlesFiltersComponent.addFilters(this.filters);

            this.loadPersons();

            if (this.filters) {
                this.disableSaveFiltersBtn = false;
            }
        }
    }

    loadPersons() {
        if (this.getPersonsSubscription) {
            this.getPersonsSubscription.unsubscribe();
        }
        this.showPersonsProgressBar = true;
        this.getPersonsSubscription = this.apiService.call('/api/integrations/needles/getPersons', {dataType: this.dataType, filters: JSON.stringify(this.filters)}).subscribe((response: any) => {
            if (response.contacts) {
                this.persons = response.contacts;
            }
            this.showPersonsProgressBar = false;
        }, () => {
            this.showPersonsProgressBar = false;
        });
    }

    showAddTagDialog() {
        this.createTagsDialogRef = this.dialog.open(CreateTagsDialogComponent, {width: '500px'});
        this.registerEventsForCreateTagsDialogRef();
    }

    registerEventsForCreateTagsDialogRef() {
        if (this.createTagsDialogSubscription) {
            this.createTagsDialogSubscription.unsubscribe();
        }
        this.createTagsDialogSubscription = this.createTagsDialogRef.afterClosed().subscribe((response: any) => {
            if (response.tags) {
                this.saveTags(response.tags);
            }
        });
    }

    saveTags(tags) {

        if (this.createTagsSubscription) {
            this.createTagsSubscription.unsubscribe();
        }

        this.showPersonsProgressBar = true;
        const contactIds = this.extractContactsIds();
        this.createTagsSubscription = this.apiService.call('/api/createTags', {
            tags: JSON.stringify(tags),
            contactIds: JSON.stringify(contactIds)
        }).subscribe((response: any) => {
            if (response.success) {

                this.generalService.showNotification('Tags are saved successfully.', 'Okay');
            }

            this.showPersonsProgressBar = false;
        }, () => {
            this.showPersonsProgressBar = false;
        });
    }

    extractContactsIds() {
        const contacts = this.persons;
        const contactIds = [];
        contacts.forEach((contact: Contact, index) => {
            contactIds.push(contact.id);
        });

        return contactIds;
    }

    onContextMenuBtnClicked(menuItem) {
        this.openAddFiltersDialog(menuItem);
    }

    onFilterRemove(index) {
        this.filters.splice(index, 1);
        if (this.filters.length > 0) {
            this.disableSaveFiltersBtn = false;
            this.loadPersons();
        } else {
            this.persons = [];
        }

    }

    saveFiltersClicked() {

        if (this.createFilterNameDialogAfterclosedSubscription) {
            this.createFilterNameDialogAfterclosedSubscription.unsubscribe();
        }

        const dialogRef = this.dialog.open(CreateFilterNameDialogComponent, {width: '350px'});

        this.createFilterNameDialogAfterclosedSubscription = dialogRef.afterClosed().subscribe((response: any) => {
            if (response && response.status) {
                this.saveFilters(response.data.name);
            }

        });

    }

    saveFilters(name) {

        if (this.saveFiltersSubscription) {
            this.saveFiltersSubscription.unsubscribe();
        }

        this.savingFilters = true;
        this.saveFiltersSubscription = this.apiService.call('/api/needles/saveFilters', {
            dataType: this.dataType,
            name,
            filters: JSON.stringify(this.filters)
        }).subscribe((response: any) => {
            if (response.success) {
                this.generalService.showNotification('Filters are saved successfully.', 'Okay');
                this.disableSaveFiltersBtn = true;
            }

            this.savingFilters = false;
        }, () => {
            this.savingFilters = false;
        });
    }

    openSavedFilters() {

        if (this.saveFiltersDialogAfterClosedSubscription) {
            this.saveFiltersDialogAfterClosedSubscription.unsubscribe();
        }

        const dialogRef = this.dialog.open(SavedFiltersDialogComponent, {width: '400px', data: {dataType: this.dataType}});

        this.saveFiltersDialogAfterClosedSubscription = dialogRef.afterClosed().subscribe((response: any) => {
            if (response && response.status) {
                this.addFilters(response.data.filters);
            }
        });
    }

    addFilters(filters) {
        this.filters = filters;
        this.NeedlesFiltersComponent.addFilters(filters);
        this.loadPersons();
    }
}



